import { _decorator, Component, Node, tween, Vec3 } from 'cc';
import { Piece } from '../Piece/Piece';
import { GridManager } from '../Grid/GridManager';
import { GameGlobal } from '../Game/GameGlobal';
const { ccclass, property } = _decorator;

@ccclass('GravityHandler')
export class GravityHandler extends Component {



    async applyGravity(matches: Piece[]) {
        const columnPieceCount = new Map<number, number>(); // Her sütundaki boşlukları tutan bir harita

        // Sütunlardaki boşlukları hesapla
        for (const piece of matches) {
            const col = piece.col;
            if (columnPieceCount.has(col)) {
                columnPieceCount.set(col, columnPieceCount.get(col) + 1);
            } else {
                columnPieceCount.set(col, 1);
            }
        }

        // Her sütun için gravity uygula
        for (const [col, emptySpaces] of columnPieceCount) {
            await this.applyGravityForSingleColumn(col, emptySpaces);
        }
    }

    async applyGravityForSingleColumn(col: number, emptySpaces: number) {
        const grid = GridManager.getInstance().grid;

        for (let row = grid.length - 1; row >= 0; row--) {
            const piece = grid[row][col];
            
            if (piece.node === null) continue; 
            console.log(piece);
            
            // const newRow = row - emptySpaces;

            // if (newRow < grid.length) {
            //     grid[newRow][col] = piece;
            //     piece.row = newRow;
                
            //     await this.movePiece(piece, newRow, col);

            //     grid[row][col] = null;
            // }
        }
    }

    movePiece(piece: Piece, newRow: number, col: number): Promise<void> {
        const newY = newRow * (GameGlobal.PIECE_CONTENT_SIZE + GameGlobal.PIECE_OFFSET);

        return new Promise<void>((resolve) => {
            tween(piece.node)
                .to(0.3, { position: new Vec3(piece.node.position.x, newY, 0) })
                .call(() => resolve())
                .start();
        });
    }
}
